nav {
  .card {
    position: relative;
    display: flex;
    flex-direction: column;
    min-width: 0;
    word-wrap: break-word;
    background-color: #fff;
    background-clip: border-box;
    border: 1px solid rgba(0, 0, 0, 0.125);
    border-radius: 0.25rem;
  }
  .card-body {
    flex: 1 1 auto;
    padding: 1.25rem;
  }
  .mt-3,
  .my-3 {
    margin-top: 1rem !important;
  }
  .mb-3,
  .my-3 {
    margin-bottom: 1rem !important;
  }
  .text-center {
    text-align: center !important;
  }
}
.flags{
  display: flex;
  float: left;
  //position: fixed;
  left: 50%;  
}
.us,.br{
  height: 40px;
  margin-left: 5px;
  margin-right: 5px;
  margin-top: 2px;
  float: left;
  //position: relative;
  left: -50%;
}

